.NavBar
{
  width: 100%;
  height: 100%;
  //display: flex;
  //align-items: stretch;
  //justify-content: center;
  .nav
  {
    // Use grid to center nav + icons as a group and keep footer bottom
    height: 100%;
    display: grid;
    // Slightly larger bottom spacer to nudge center group up
    grid-template-rows: 0.9fr auto 1.1fr auto; // top spacer, center group, bottom spacer, footer
    align-items: center;
    justify-items: center;
    flex-shrink: 0;
    .hero
    {
      //margin-top: 10rem;
      position: absolute;
      //top: 10%;
      //width: 60%;
      text-align: center;

      .hero-text
      {
        color: var(--font-color1);
        text-align: center;
        text-transform: uppercase;
        font-size: 2rem;
        position: relative;
        padding-bottom: 1rem;
        letter-spacing: 3px;
        cursor: default;
        width: 100%;
        margin: auto;
      }

      .home-sub-text
      {
        color: inherit;
        text-align: center;
        width: 60%;
        margin: auto;
      }

      .icons
      {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 1.5rem 0;

        .icon-link
        {
          margin-right: 1rem;
          .icon
          {
            font-size: 2.8rem;
            color: var(--font-color2);
            transition: all .3s ease-in-out;
          }

          .icon:hover
          {
            color: var(--main-color)
          }
        }
      }
    }

    .nav-center
    {
      grid-row: 2;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      .nav-items
      {
        width: 100%;
        padding-top: 2.5rem;
        padding-bottom: 0.5rem;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        .nav-item
        {
          width: 100%;
          list-style: none;
          text-align: center;
          a
          {
            text-decoration: none;
            font-size: inherit;
            color: var(--font-color1);
            text-align: center;
            display: block;
            padding: 1.3rem 0;
            position: relative;
            &::before
            {
              content: "";
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              background-color: var(--main-color-hover);
              transform-origin: bottom;
              transform: scale(0);
              overflow-x: hidden;
              transition: transform .6s, .2s, width .2s cubic-bezier(1,-.16,0,1.32);
            }
            &:hover::before
            {
              transform: scale(1);
              width: 100%;
            }
          }
        }
      }
    }

    // Social icons placed directly below nav items inside the centered group
    .icons
    {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 1.5rem 0;

      .icon-link
      {
        margin-right: 1rem;
        .icon
        {
          font-size: 2.8rem;
          color: var(--font-color2);
          transition: all .3s ease-in-out;
        }

        .icon:hover
        {
          color: var(--main-color)
        }
      }
    }

  }
  .footer
  {
    grid-row: 4;
    // Footer is placed on the 4th grid row by parent .nav
    margin-top: 0; // override previous auto margin to work with grid
    cursor: default;
    width: 100%;
    p
    {
      color: var(--main-border-color);
      font-size: .8rem;
      text-align: center;
      padding: 1rem 0;
    }
  }
  .active
  {
    background-color: var(--main-color);
    transition: .3s all ease-in-out;
  }
}
